<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="150">
        <FormItem label="标题：">
          <label>{{resData.lawTitle}}</label>
        </FormItem>
        <FormItem label="是否公开：">
          <label>{{resData.ifShow=='true'?'是':'否'}}</label>
        </FormItem>
        <FormItem label="发布机构：">
          <label>{{resData.pubDept}}</label>
        </FormItem>
        <FormItem label="发布时间：">
          <label>{{resData.pubTime}}</label>
        </FormItem>
        <FormItem label="生效时间：">
          <label>{{resData.enabledTime}}</label>
        </FormItem>
        <FormItem label="失效时间：">
          <label>{{resData.diabledTime}}</label>
        </FormItem>
        <FormItem label="文件类型：">
          <label>{{resData.fileType}}</label>
        </FormItem>
        <FormItem label="地区范围：">
          <label>{{resData.areaScope}}</label>
        </FormItem>
        <FormItem label="行业：">
          <label>{{resData.industry}}</label>
        </FormItem>
        <FormItem label="文号：">
          <label>{{resData.fileNum}}</label>
        </FormItem>
        <FormItem label="联系人：">
          <label>{{resData.linkman}}</label>
        </FormItem>
        <FormItem label="联系电话：">
          <label>{{resData.phone}}</label>
        </FormItem>
        <FormItem label="文件来源：">
          <label>{{resData.source}}</label>
        </FormItem>
        <FormItem label="法规资料库类别：">
          <label>{{resData.lawType}}</label>
        </FormItem>
        <FormItem label="编辑内容">
          <label>{{resData.content}}</label>
        </FormItem>
        <FormItem label="附件：">
          <showUsualFiles :file-obj="fileObj" :key="keyId"></showUsualFiles>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";

export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      keyId: "",
      resData: {},
      fileObj:{},
    };
  },
  created() {
    if (Object.keys(this.dataObj.data).length > 0) {
      let datas = this.dataObj.data;
      if (datas && datas.id) {
        // 调用浏览次数接口
        if(this.dataObj && this.dataObj.infViewNum){
          this.getInfInfoRowNum(datas.id);
        }else{
          this.getInfInfoRow(datas.id);
        }
      }
    }
  },
  methods: {
    getInfInfoRowNum(id){
      this.axios
        .post("/process/infLaws/getInfLawsRow/" + id +'?infViewNum=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    },
    getInfInfoRow(id) {
      this.axios
        .post("/process/infLaws/getInfLawsRow/" + id +'?decode=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    }
  }
};
</script>


