<template>
  <div class="add-reg-manage">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      class="form-content"
    >
      <FormItem label="标题" prop="lawTitle">
        <Input v-model="formValidate.lawTitle" placeholder="请输入标题"></Input>
      </FormItem>
      <FormItem label="是否公开" prop="ifShow">
        <i-switch v-model="formValidate.ifShow" @on-change="changeUseSts(formValidate.ifShow)">
          <span slot="open">是</span>
          <span slot="close">否</span>
        </i-switch>
      </FormItem>
      <FormItem label="发布机构" prop="pubDept">
        <Select v-model="formValidate.pubDept" placeholder="请选择发布机构">
          <Option value>未选择</Option>
          <Option v-for="item in pubDeptDatas" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="发布时间" prop="pubTime">
        <DatePicker
          type="date"
          placeholder="请选择发布时间"
          :value="formValidate.pubTime"
          @on-change="formValidate.pubTime=$event"
        ></DatePicker>
      </FormItem>
      <FormItem label="生效时间" prop="enabledTime">
        <DatePicker
          type="date"
          placeholder="请选择生效时间"
          :value="formValidate.enabledTime"
          @on-change="formValidate.enabledTime=$event"
        ></DatePicker>
      </FormItem>
      <FormItem label="失效时间" prop="diabledTime">
        <DatePicker
          type="date"
          placeholder="请选择生效时间"
          :value="formValidate.diabledTime"
          @on-change="formValidate.diabledTime=$event"
        ></DatePicker>
      </FormItem>
      <FormItem label="文件类型" prop="fileType">
        <Select v-model="formValidate.fileType" placeholder="请选择文件类型">
          <Option value>未选择</Option>
          <Option v-for="item in fileTypeDatas" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="地区范围" prop="areaScope">
        <Select v-model="formValidate.areaScope" placeholder="请选择地区范围" >
            <Option value>未选择</Option>
            <Option
              v-for="item in regionLsit"
              :value="item.code"
              :key="item.code"
            >{{item.name}}</Option>
          </Select>
      </FormItem>
      <FormItem label="行业" prop="industry">
        <Input v-model="formValidate.industry" placeholder="请输入行业"></Input>
      </FormItem>
      <FormItem label="文号" prop="fileNum">
        <Input v-model="formValidate.fileNum" placeholder="请输入文号"></Input>
      </FormItem>
      <FormItem label="联系人" prop="linkman">
        <Input v-model="formValidate.linkman" placeholder="请输入联系人"></Input>
      </FormItem>
      <FormItem label="联系电话" prop="phone">
        <Input v-model="formValidate.phone" placeholder="请输入联系电话"></Input>
      </FormItem>
      <FormItem label="文件来源" prop="source">
        <Input v-model="formValidate.source" placeholder="请输入文件来源"></Input>
      </FormItem>
      <FormItem label="法规资料库类别" prop="lawType">
        <Input v-model="formValidate.lawType" placeholder="请输入法规资料库类别"></Input>
      </FormItem>
      <FormItem label="编辑内容" prop="content">
        <Input
          v-model="formValidate.content"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入编辑内容"
        ></Input>
      </FormItem>
      <FormItem label="附件">
        <showUsualFiles :file-obj="fileObj" :key="fileId" @requestUploadList="receiveUploadList"></showUsualFiles>
      </FormItem>
      <FormItem>
        <div class="button-content mL_0">
          <Button type="primary" @click="handleSubmit('formValidate')" :disabled="buttonDisabled">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  components: {
    showUsualFiles
  },
  props: ["dataObj"],
  data() {
    return {
      buttonDisabled:false,
      regionLsit: [], //地区范围
      uploadList: [],
      fileObj: {},
      fileId: "",
      formValidate: {
        lawTitle: "", //标题
        pubDept: "", //发布机构
        ifShow: false, //是否公开
        pubTime: "", //发布时间
        enabledTime: "", //生效时间
        diabledTime: "", //失效时间
        fileType: "", //文件类型
        areaScope: "", //地区范围
        industry: "", //行业
        fileNum: "", //文号
        linkman: "", //联系人
        phone: "", //联系电话
        source: "", //文件来源
        lawType: "", //法规资料库类别
        content: "", //编辑内容
        attachId: "" //文件id
      },
      pubDeptDatas: [],
      fileTypeDatas: [],
      index: 1,
      ruleValidate: {
        lawTitle: [
          {
            required: true,
            message: "标题不能为空！",
            trigger: "blur"
          },
          { max: 120, message: "输入长度过长", trigger: "blur" }
        ],
        areaScope: [{ max: 125, message: "输入长度过长", trigger: "blur" }],
        industry: [{ max: 125, message: "输入长度过长", trigger: "blur" }],
        fileNum: [{ max: 125, message: "输入长度过长", trigger: "blur" }],
        linkman: [{ max: 29, message: "输入长度过长", trigger: "blur" }],
        phone: [{ max: 29, message: "输入长度过长", trigger: "blur" }],
        source: [{ max: 125, message: "输入长度过长", trigger: "blur" }],
        lawType: [{ max: 125, message: "输入长度过长", trigger: "blur" }],
        pubDept: [
          {
            required: true,
            message: "发布机构不能为空！",
            trigger: "change"
          }
        ],
        pubTime: [
          {
            required: true,
            message: "发布时间不能为空！",
            trigger: "change"
          }
        ],
        enabledTime: [
          {
            required: true,
            message: "生效时间不能为空！",
            trigger: "change"
          }
        ],
        content: [
          {
            required: false,
            message: "编辑内容不能超过1000字",
            trigger: "blur",
            max: 1000
          }
        ],
        phone: [
          {
            required: false,
            message: "联系电话不能超过1000字",
            trigger: "blur",
            max: 1000
          },
          {
            required: false,
            pattern: /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/,
            message: "联系电话格式不正确",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    this.getRegion();
    this.pubDeptDatas.length > 0 ? "" : this.getDic();
    // 1:新增 2修改
    if (this.dataObj.type == "2") {
      let datas = this.dataObj.data;
      if (datas && datas.id) {
        this.getInfInfoRow(datas.id);
      }
    } else {
      this.fileObj = {
        data: [],
        showFlag: true
      };
    }
  },
  mounted() {},
  methods: {
    getRegion() {
      this.axios
        .post("/process/getProcessDic/region")
        .then(response => {
          if (response.code == "200") {
            this.regionLsit = response.data;
          }
        })
        .catch(function(error) {});
    },
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidate.attachId = this.uploadList;
      console.log("receiveUploadlist", this.uploadList);
    },
    getInfInfoRow(id) {
      this.axios
        .post("/process/infLaws/getInfLawsRow/" + id, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            let item = response.data;
            this.formValidate = {
              attachId: item.attachId,
              lawTitle: item.lawTitle, //标题
              pubDept: item.pubDept, //发布机构
              ifShow: item.ifShow == "true" ? true : false, //是否公开
              pubTime: item.pubTime, //发布时间
              enabledTime: item.enabledTime, //生效时间
              diabledTime: item.diabledTime, //失效时间
              fileType: item.fileType, //文件类型
              areaScope: item.areaScope, //地区范围
              industry: item.industry, //行业
              fileNum: item.fileNum, //文号
              linkman: item.linkman, //联系人
              phone: item.phone, //联系电话
              source: item.source, //文件来源
              lawType: item.lawType, //法规资料库类别
              content: item.content, //编辑内容
              attachId: item.attachId, //文件id
              id: item.id
            };
            this.fileObj = {
              data: item,
              showFlag: true
            };
            this.fileId = item.busId;
          }
        })
        .catch(error => {});
    },
    /**
     * name:获取下拉字典
     */
    getDic() {
      this.axios
        .get("/system/getDic/PROCESS_PUB_ORG,PROCESS_FILE_TYPE")
        .then(response => {
          if (response.code == "200") {
            this.pubDeptDatas = response.data.PROCESS_PUB_ORG;
            this.fileTypeDatas = response.data.PROCESS_FILE_TYPE;
          }
        })
        .catch(function(error) {});
    },
    changeUseSts(flg) {
      this.formValidate.ifShow = flg;
    },
    /**
     * name:保存新增信息
     **/
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          // 新增
          if (this.dataObj.type == "1") {
            this.buttonDisabled = true;
            this.axios
              .post("/process/infLaws/addInfLaws", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.buttonDisabled = false;
                  this.$emit("save");
                  this.$Message.success("保存成功！");
                }
              })
              .catch(error => {
                this.buttonDisabled = false;
              });
          } else {
            // 修改
            this.buttonDisabled = true;
            this.axios
              .post("/process/infLaws/updateInfLaws", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.buttonDisabled = false;
                  this.$emit("save");
                  this.$Message.success("修改成功！");
                } else {
                  this.buttonDisabled = true;
                  this.$Message.info(response.message);
                }
              })
              .catch(error => {
                this.buttonDisabled = false;
              });
          }
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-reg-manage {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .special-div {
      width: 100px;
      font-size: 14px;
      padding: 10px 12px 10px 0;
      text-align: right;
    }
    .special-form {
      margin: 0 0 20px -20px;
      border: 1px solid #eeeeee;
      padding: 20px 20px 0px 0px;
    }
  }
}
</style>


