<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">法规库管理</span>
      <div class="button-content">
        <Button type="primary" @click="operate('1')">
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button type="primary" @click="exportsFile" v-authcontrol="{menuCode:'108001004004'}">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="标题：">
                <Input v-model="formValidate.lawTitle" placeholder="请输入标题" style="width:200px"></Input>
              </FormItem>
              <FormItem label="管理部门：" >
                <treeselect
                  v-model="formValidate.manager"
                  valueConsistsOf="ALL" 
              :limit="3"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择管理部门"
                  style="width:200px"
                />
              </FormItem>
              <FormItem label="发布机构" prop="pubDept">
                <Select v-model="formValidate.pubDept" placeholder="请选择发布机构" style="width:200px">
                  <Option value>未选择</Option>
                  <Option
                    v-for="item in pubDeptDatas"
                    :value="item.code"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="发布时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择发布时间"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="文件类型：">
                <Select v-model="formValidate.fileType" placeholder="请选择文件类型" style="width:200px">
                  <Option value>未选择</Option>
                  <Option
                    v-for="item in fileTypeDatas"
                    :value="item.code"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="地区范围：">
                <Select v-model="formValidate.areaScope" placeholder="请选择地区范围" style="width:200px">
                  <Option value>未选择</Option>
                  <Option
                    v-for="item in regionLsit"
                    :value="item.code"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" style="width:89px" @click="handleSubmit(0, 1)">查询</Button>
                <Button type="primary" style="margin-left:10px" @click="handleSubmit(1, 1)">精确查询</Button>
              </div>
            </Row>
          </Form>
          <div class="contentCellBody">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <Row>
              <table class="tableBox">
                <thead>
                  <tr>
                    <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                  </tr>
                </thead>
                <tbody>
                  <template>
                    <tr v-for="(item,index) in tableBody" :id="item.documentId">
                      <td>{{pageSize*(pageNum-1)+index+1}}</td>
                      <td class="noWrap">
                        <span class="spanBox" @click="operate('2', item)">修改</span>
                        <span class="spanBox" @click="operate('3',item)">删除</span>
                      </td>
                      <td>
                        <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.lawTitle"
                        >
                            <span
                              slot="reference"
                              class="spanBox textOverflow  disBlock textMaxWidth"
                              @click="operate('4', item)"
                            >{{item.lawTitle}}</span>
                        </el-popover>
                      </td>
                      <td>{{item.ifShow=='true'?'是':'否'}}</td>
                      <td>{{item.manager}}</td>
                      <td>
                          <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.pubDept"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.pubDept}}</span>
                        </el-popover>
                      </td>
                      <td>{{item.pubTime}}</td>
                      <td class="noWrap">{{item.enabledTime}}</td>
                      <td class="noWrap">{{item.diabledTime}}</td>
                      <td>{{item.fileType}}</td>
                      <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.areaScope"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.areaScope}}</span>
                        </el-popover>
                      </td>
                      
                       <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.industry"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.industry}}</span>
                        </el-popover>
                      </td>
                   
                      <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.fileNum"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.fileNum}}</span>
                        </el-popover>
                      </td>
                  
                      <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.linkman"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.linkman}}</span>
                        </el-popover>
                      </td>
                      <td>{{item.phone}}</td>
                    
                      <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.source"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.source}}</span>
                        </el-popover>
                      </td>
                     
                      <td>
                         <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.lawType"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.lawType}}</span>
                        </el-popover>
                      </td>
                      <td>
                        <el-popover
                          placement="top-start"
                          title
                          width="200"
                          trigger="hover"
                          :content="item.content"
                        >
                          <span slot="reference" class="textOverflow disBlock textMaxWidth">{{item.content}}</span>
                        </el-popover>
                      </td>
                      <td class="spanBox" @click="watchFiles(item)">查看</td>
                      <td class="noWrap">{{item.createTime}}</td>
                      <td>{{item.creator}}</td>
                      <td class="noWrap">{{item.updateTime}}</td>
                      <td>{{item.updater}}</td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </Row>
          </div>
          <div class="flexCenter pageBox">
            <Page
              :total="rowCount"
              :page-size="pageSize"
              @on-change="pageChange"
              @on-page-size-change="pageSizefun"
              :current.sync="currentpage"
              show-elevator
              show-sizer
            ></Page>
          </div>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: percentWidth}"
    >
      <component
        :is="tabview"
        ref="dialogModal"
        @cancel="cancelDialog"
        @save="saveDatas"
        :file-obj="dataObj"
        :data-obj="dataObj"
        :key="keyId"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addRegDataManage from "./common/regDataManage/addRegDataManage";
import regDataManageDetail from "./common/regDataManage/regDataManageDetail";
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  created() {
    this.getDic();
    this.getRegion();
    this.handleSubmit(0, 1);
    this.getExtractUnit();
  },
  computed: {},
  components: {
    addRegDataManage,
    regDataManageDetail,
    showUsualFiles,
    Treeselect
  },
  watch: {},
  data() {
    return {
      ifExacte:0,
      busiSelect: [],
      percentWidth: "60%",
      keyId: "",
      modelTitle: "新增",
      tabview: "addRegDataManage",
      showModal: false,
      data1: [],
      dataObj: {
        type: "1", //1新增，2修改，3删除 4详情
        data: [],
        showFlag: true
      },
      regionLsit: [], //地区范围
      fileTypeDatas: [], //文件类型
      pubDeptDatas: [], //发布机构
      pageSize: 10,
      pageNum: 1,
      rowCount: 0,
      currentpage: 1,
      formValidate: {
        lawTitle: "", //标题
        manager: [], //管理部门
        pubDept: "", //发文部门
        permissionTime: ["", ""],
        fileType: "", //文件类型
        areaScope: "" //地区范围
      },
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "标题" },
        { name: "是否公开" },
        { name: "管理部门" },
        { name: "发布机构" },
        { name: "发布时间" },
        { name: "生效时间" },
        { name: "失效时间" },
        { name: "文件类型" },
        { name: "地区范围" },
        { name: "行业" },
        { name: "文号" },
        { name: "联系人" },
        { name: "联系电话" },
        { name: "文件来源" },
        { name: "法规资料库类别" },
        { name: "编辑内容" },
        { name: "附件" },
        { name: "入库时间" },
        { name: "入库人" },
        { name: "更新时间" },
        { name: "更新人" }
      ],
      tableBody: []
    };
  },
  methods: {
    exportsFile(){
       let that = this;
       let data = {
        manager:this.formValidate.manager,
        ifExacte: this.ifExacte,
        minPubTime: this.formValidate.permissionTime[0],
        maxPubTime: this.formValidate.permissionTime[1],
        pubDept: this.formValidate.pubDept ? this.formValidate.pubDept : "",
        fileType: this.formValidate.fileType ? this.formValidate.fileType : "",
        areaScope: this.formValidate.areaScope
          ? this.formValidate.areaScope
          : "",
        lawTitle: this.formValidate.lawTitle.trim(),
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
       this.axios
      .post("/process/infLaws/getInfLawsExcelList",data, {
        headers: { "Content-Type": "application/json" },
        responseType: "blob"
      })
      .then(response => {
         this.download(response, '导出文档.xls');
      })
      .catch(error => {});
    },

    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data],{type: 'application/vnd.ms-excel'}));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },

    watchFiles(item) {
      this.percentWidth = "40%";
      this.modelTitle = "文件列表";
      this.tabview = "showUsualFiles";
      this.showModal = true;
      this.keyId = item.busId;
      this.dataObj = {
        data: item,
        showFlag: false
      };
    },
    pageSizefun(pageSize) {
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo) {
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo);
    },
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo) {
      let data = {
        manager:this.formValidate.manager,
        ifExacte: ifExacte,
        minPubTime: this.formValidate.permissionTime[0],
        maxPubTime: this.formValidate.permissionTime[1],
        pubDept: this.formValidate.pubDept ? this.formValidate.pubDept : "",
        fileType: this.formValidate.fileType ? this.formValidate.fileType : "",
        areaScope: this.formValidate.areaScope
          ? this.formValidate.areaScope
          : "",
        lawTitle: this.formValidate.lawTitle.trim(),
        pageNum: pageNo,
        pageSize: this.pageSize
      };
      this.axios
        .post("/process/infLaws/getInfLawsList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.tableBody = response.data.records;
            this.tableBody.forEach(element => {
              element.createTime = element.createTime.substr(0, 19);
              element.updateTime = element.updateTime.substr(0, 19);
              element.diabledTime = element.diabledTime.substr(0, 19);
              element.enabledTime = element.enabledTime.substr(0, 19);
              element.createTime = element.createTime.substr(0, 19);
            });
            this.rowCount = response.data.total;
          }
        })
        .catch(error => {});
    },
    /**
     * name:获取下拉字典
     */
    getDic() {
      this.axios
        .get("/system/getDic/PROCESS_PUB_ORG,PROCESS_FILE_TYPE,region")
        .then(response => {
          if (response.code == "200") {
            this.pubDeptDatas = response.data.PROCESS_PUB_ORG;
            this.fileTypeDatas = response.data.PROCESS_FILE_TYPE;
          }
        })
        .catch(function(error) {});
    },
    getRegion() {
      this.axios
        .post("/process/getProcessDic/region")
        .then(response => {
          if (response.code == "200") {
            this.regionLsit = response.data;
          }
        })
        .catch(function(error) {});
    },
    getExtractUnit() {
      this.axios
        .post("/system/unit/getExtractUnitAll")
        .then(response => {
          if (response.code == "200") {
            this.data1 = response.data;
            this.selectTreeCleanUp(this.data1);
          }
        })
        .catch(error => {});
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    deleteItem(item) {
      console.log("item", item);
      if (item.id) {
        this.axios
          .post(
            "/process/infLaws/deleteInfLaws",
            { id: item.id },
            {
              headers: { "Content-Type": "application/json" }
            }
          )
          .then(response => {
            this.$Message.success("删除成功！");
            this.handleSubmit(0, 1);
            this.pageNum = 1;
            this.currentpage = 1;
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    operate(type, item) {
      switch (type) {
        case "1":
          this.keyId = "";
          this.percentWidth = "60%";
          this.modelTitle = "新增";
          this.tabview = "addRegDataManage";
          this.showModal = true;
          this.dataObj = {
            type: 1,
            data: [],
            showFlag: true
          };
          break;
        case "2":
          this.keyId = item.id;
          this.percentWidth = "60%";
          this.modelTitle = "修改";
          this.dataObj = {
            type: 2,
            data: item,
            showFlag: true
          };
          this.tabview = "addRegDataManage";
          this.showModal = true;
          break;
        case "3":
          this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.deleteItem(item);
            },
            onCancel: () => {}
          });
          break;
        case "4":
          this.percentWidth = "50%";
          this.keyId = item.id;
          this.modelTitle = "详情";
          this.dataObj = {
            type: 4,
            data: item,
            showFlag: false
          };
          this.tabview = "regDataManageDetail";
          this.showModal = true;
          break;
      }
    },
    saveDatas() {
      this.showModal = false;
      this.pageNum = 1;
      this.currentpage = 1;
      this.handleSubmit(0, 1);
      this.cancel();
    },
    cancelDialog(val) {
      this.showModal = val;
      this.cancel();
    },
    cancel() {
      if (this.$refs["dialogModal"].$refs["formValidate"]) {
        this.$refs["dialogModal"].$refs["formValidate"].resetFields();
        this.keyId = !this.keyId;
      }
    }
  }
};
</script>



